const charDic = require('./dictionary/char')
const wordDic = require('./dictionary/word')
const T = {}

T.charMap = function (char) {
  const t = charDic[char]

  return (typeof t === 'undefined') ? char : t
}

T.wordMap = function (text) {
console.log(text, wordDic)
}

T.textMap = function (text) {
  const result = T.wordMap(text)
  const chars = Array.from(result).map(char => T.charMap(char))

  return chars.join('')
}

T.tw2cn = function (text) {
  return T.textMap(text)
}

module.exports = T
